import { Component, OnInit, OnDestroy } from '@angular/core';
import {
  NgbModal,
  ModalDismissReasons
} from '@ng-bootstrap/ng-bootstrap';
import { faPlus, faMinus, faShoppingCart, faShoppingBasket } from '@fortawesome/free-solid-svg-icons';
import { ActivatedRoute, Router } from '@angular/router';
import { HttpService } from '../services/http.service';
import { environment } from 'src/environments/environment';
import { AuthService } from '../services/auth.service';
import * as _ from 'lodash';
import { DataService } from '../services/data.service';

@Component({
  selector: 'app-program',
  templateUrl: './program.component.html',
  styleUrls: ['./program.component.scss'],
})
export class ProgramComponent implements OnInit, OnDestroy {
  products = [];
  closeResult = '';
  faPlus = faPlus;
  faMinus = faMinus;
  faShoppingCart = faShoppingCart;
  faShoppingBasket = faShoppingBasket;
  productAdvantages: [];
  basketItems = 0;
  minBasketItems = 0;
  cartAlert: boolean = false;
  private activeProgram: any;
  selectedItemCount: number = 0;
  selectedItemIndex: number = 0;

  constructor(private modalService: NgbModal, private activatedRoute: ActivatedRoute, private _httpService: HttpService, private authService: AuthService, private router: Router, private dataService: DataService) { }

  ngOnInit(): void {
    this.activatedRoute.queryParams.subscribe(params => {
      const programId = parseInt(params['programId']);
      this.getProducts(programId);
      this.dataService.program = this.dataService.programList[programId.toString()][0];
      this.setPageData();
    });
  }
  // ========================================================================================================
  private setPageData() {
    this.activeProgram = this.dataService.program;
    this.minBasketItems = this.activeProgram['min_buy_criteria'] ? this.activeProgram['min_buy_criteria'] : 0;
  }
  // ========================================================================================================
  open(ref: any, index: number) {
    this.productAdvantages = this.products[index]['advantages'];
    this.selectedItemIndex = index;
    this.selectedItemCount = this.products[index]['total'];
    this.modalService.open(ref, { size: 'lg' }).result.then(
      (result) => {
        this.closeResult = `Closed with: ${result}`;
      },
      (reason) => {
        this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
      }
    );
  }
  // ========================================================================================================
  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }
  // ========================================================================================================
  private getProducts(programId: number) {
    this._httpService.getRequest(environment.PRODUCTS_LIST + "?programId=" + programId, this.authService.token).subscribe((res) => {
      let body = res.body;
      this.products = body['data'];
      let cartArr = this.dataService.cartData;
      // let cartObj = localStorage.getItem('cart');
      if (cartArr) {
        // let cartArr = JSON.parse(cartObj);
        let o = _.groupBy(cartArr, (item: any) => item.id);
        if (o.hasOwnProperty(this.activeProgram['programid'])) {
          this.getCartItemsfromStorage(o);
          this.setDefaultValue();
        } else {
          this.setDefaultValue();
        }
      } else {
        this.setDefaultValue();
      }
      this.validateBasket();
    }, (error: Error) => { 
      this.dataService.handleErrorResponse(error);
    });
  }
  // ========================================================================================================
  basketOps(index: number, operation: string) {
    if (operation === 'Add') {
      this.products[index]['total'] += 1;
    } else {
      this.products[index]['total'] -= 1;
    }
    this.selectedItemCount = this.products[index]['total'];
    this.validateBasket();
  }
  // ========================================================================================================
  private validateBasket() {
    this.basketItems = 0;
    this.products.forEach((el: any) => {
      if (el.total > 0) {
        this.basketItems += el.total;
      }
    });
    if(this.basketItems > 0 && this.basketItems < this.minBasketItems) {
      this.dataService.displayAlert('info', 'MINIMUM 2 ITEMS TO BE ADDED IN THE BASKET IN THIS PROGRAM IN ORDER TO BE ABLE TO PROCEED TO CART', 'toast-top-right');
    }

    if(this.basketItems > 0 && this.basketItems >= this.minBasketItems) {
      this.dataService.displayAlert('success', 'YOU CAN PROCEED TO CART NOW', 'toast-top-right');
    }
  }
  // ========================================================================================================
  private getCartItemsfromStorage(o: any) {
    let items = o[this.activeProgram['programid'].toString()][0]['products'];
    let p = _.groupBy(items, (item: any) => item.productid);
    this.products.forEach((el: any) => {
      if (p.hasOwnProperty(el.productid.toString())) {
        el.total = p[el.productid.toString()][0].total;
      }
    });
  }
  // ========================================================================================================
  goToCart() {
    let items = this.getbasketItems(this.products);
    // let cartObj = localStorage.getItem('cart');
    let cartArr = this.dataService.cartData;
    if (cartArr) {
      // let cartArr = JSON.parse(cartObj);
      let o = _.groupBy(cartArr, (item: any) => item.id);
      if (o.hasOwnProperty(this.activeProgram['programid'])) {
        cartArr.forEach((element: any) => {
          if (element.id === this.activeProgram['programid']) {
            element.products = items;
          }
        });
      } else {
        cartArr.push({ name: this.activeProgram['name'], id: this.activeProgram['programid'], products: items, price: 0 });
      }
      this.setCartStorage(cartArr);
    } else {
      // localStorage.setItem('cart', JSON.stringify([{ name: this.activeProgram['name'], id: this.activeProgram['programid'], products: items, price: 0 }]));
      this.setCartStorage([{ name: this.activeProgram['name'], id: this.activeProgram['programid'], products: items, price: 0 }]);
    }
    if (this.authService.isLoggedIn) {
      this.router.navigate(['/cart'], { relativeTo: this.activatedRoute });
    } else {
      this.router.navigate(['/login'], { relativeTo: this.activatedRoute });
    }
  }
  // ========================================================================================================
  private getbasketItems(arr: any) {
    let items = [];
    arr.forEach((element: any) => {
      if (element.total > 0) {
        items.push(element);
      }
    });
    return items;
  }
  // ========================================================================================================
  private setDefaultValue() {
    this.products.forEach((el: any) => {
      if(!el.hasOwnProperty('total')) {
        el.total = 0;
      }
    });
  }
// ========================================================================================================
  private setCartStorage(arr: any){
    localStorage.setItem('cart', JSON.stringify(arr));
    this.dataService.cartData = arr;
    this.dataService.cartItems = arr.length;
  }
  // ========================================================================================================
  ngOnDestroy(): void {
    delete this.products;
    delete this.closeResult;
    delete this.productAdvantages;
    delete this.basketItems;
    delete this.minBasketItems;
    delete this.cartAlert;
    delete this.activeProgram;
    delete this.selectedItemCount;
    delete this.selectedItemIndex;

    delete this.faMinus;
    delete this.faMinus;
    delete this.faShoppingBasket;
    delete this.faShoppingCart;
  }
}
