import { Component, OnInit, OnDestroy } from '@angular/core';
import { HttpService } from '../services/http.service';
import { environment } from 'src/environments/environment';
import { AuthService } from '../services/auth.service';
import * as _ from 'lodash';
import { DataService } from '../services/data.service';

@Component({
  selector: 'app-cart',
  templateUrl: './cart.component.html',
  styleUrls: ['./cart.component.scss']
})
export class CartComponent implements OnInit, OnDestroy {

  cartItems: any[] = [];
  subscriptions: any[] = [];
  subscriptionMap: Map<number, string[]>;
  priceMap: Map<number, number>;
  totalPrice: number = 0;

  constructor(private _httpService: HttpService, private authService: AuthService, private dataService: DataService) { }

  ngOnInit(): void {
    this.getSubscriptions();
    let storageItem = this.dataService.cartData;
    if (storageItem) {
      this.cartItems = storageItem;
    } else {
      this.cartItems = [];
    }
    this.dataService.cartItems = this.cartItems.length;
  }
  // ========================================================================================================
  private getSubscriptions() {
    this._httpService.getRequest(environment.SUBSCRIPTIONS, this.authService.token).subscribe((res) => {
      let body = res.body;
      this.subscriptions = body['data'].sort(this._httpService.GetSortOrder('subscriptionid'));
      this.subscriptionMap = new Map<number, string[]>();
      this.priceMap = new Map<number, number>();
      let g = _.groupBy(this.subscriptions, (item: any) => item.programid);
      for (let key in g) {
        let n = [];
        g[key].forEach((element: any, index: number) => {
          if (index === 0) {
            this.priceMap.set(Number(key), Number(element.price));
          }
          n.push(element.package.toUpperCase());
        });
        this.subscriptionMap.set(Number(key), n);
      }
      if (this.cartItems.length > 0) {
        this.totalPrice = 0;
        for (let value of this.priceMap.values()) {
          this.totalPrice += value;
        }
        this.cartItems.forEach((el: any) => {
          el.packages = this.subscriptionMap.get(el.id);
        });
      }
    }, (error: Error) => {
      this.dataService.handleErrorResponse(error);
    });
  }
  // ========================================================================================================
  onChange(event: any, programId: number, index: number) {
    let packageName: string = (event.target.value).toLowerCase();
    this.subscriptions.forEach((el: any) => {
      if (el.package === packageName && el.programid === programId) {
        this.priceMap.set(programId, el.price);
        this.cartItems[index].price = el.price;
      }
    });
    this.totalPrice = 0;
    this.cartItems.forEach((el) => {
      this.totalPrice += el.price;
    });
  }
  // ========================================================================================================
  getSubscriptionProgramsWise(programId: number) {
    return this.subscriptionMap.get(programId);
  }
  // ========================================================================================================
  getSubscriptionCost(programId: number) {
    try {
      return this.priceMap.get(programId);
    } catch (ex) {
      return 0;
    }
  }
  // ========================================================================================================
  ngOnDestroy(): void {
    delete this.cartItems;
    delete this.subscriptions;
    delete this.priceMap;
    delete this.subscriptionMap;
    delete this.totalPrice;
  }
  // ========================================================================================================
}
