import { NgModule } from '@angular/core';
import { Routes, RouterModule, NoPreloading  } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { ProgramComponent } from './program/program.component';
import { CartComponent } from './cart/cart.component';
import { AuthGuardService } from './services/auth-guard.service';


const routes: Routes = [
  { path: '', redirectTo: 'login', pathMatch: 'full'},
  { path: 'login', component: LoginComponent},
  { path: 'profile', canLoad: [AuthGuardService], canActivate: [AuthGuardService], loadChildren: () => import('./profile/profile.module').then(m => m.ProfileModule)},
  { path: 'orders', canLoad: [AuthGuardService], canActivate: [AuthGuardService], loadChildren: () => import('./orders/orders.module').then(m => m.OrdersModule)},
  { path: 'program', component: ProgramComponent},
  { path: 'cart', component: CartComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
