import { Injectable } from '@angular/core';
import { AuthService } from './auth.service';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastrService } from 'ngx-toastr';

@Injectable({
  providedIn: 'root',
})
export class DataService {
  program: any;
  programList: any;
  cartData: any;
  cartItems: number = 0;

  constructor(private alertService: ToastrService, private authService: AuthService, private router: Router, private activatedRoute: ActivatedRoute) { }

  displayAlert(type: string, message: string, position: string) {
    switch (type) {
      case 'success': this.alertService.success(message, 'Success', { timeOut: 5000, positionClass: position }); break;
      case 'error': this.alertService.error(message, 'Error', { timeOut: 5000, positionClass: position }); break;
      case 'warning': this.alertService.warning(message, 'Warning', { timeOut: 5000, positionClass: position }); break;
      case 'info': this.alertService.info(message, 'Information', { timeOut: 5000, positionClass: position }); break;
    }
  }

  handleErrorResponse(error: any) {
    if (error.status === 401) {
      this.authService.isLoggedIn = false;
      this.authService.token = undefined;
      this.authService.clientName = '';
      this.authService.usrObj = null;
      this.router.navigate(['/login'], { relativeTo: this.activatedRoute });
    }
    this.displayAlert('error', error.error.message, 'toast-top-right');
    console.log(error);
  }
}
