import { Component, OnInit, OnDestroy } from '@angular/core';
import { faShoppingCart, faBoxOpen, faIdCard, faBell, faPowerOff } from '@fortawesome/free-solid-svg-icons';
import { AuthService } from './services/auth.service';
import { Router, ActivatedRoute } from '@angular/router';
import { environment } from '../environments/environment';
import { HttpService } from './services/http.service';
import * as _ from 'lodash';
import { DataService } from './services/data.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
})
export class AppComponent implements OnInit, OnDestroy {
  faShoppingCart = faShoppingCart;
  faBoxOpen = faBoxOpen;
  faIdCard = faIdCard;
  faBell = faBell;
  faPowerOff = faPowerOff;
  programList = [];

  constructor(public authService: AuthService, private router: Router, private activatedRoute: ActivatedRoute, private _httpService: HttpService, public dataService: DataService) { }

  ngOnInit() {
    this.getPrograms();
  }

  logout() {
    this.authService.logout(environment.SIGN_OUT).subscribe((res: any) => {
      let body = res.body;
      this.authService.isLoggedIn = false;
      this.authService.token = undefined;
      this.authService.clientName = '';
      this.authService.usrObj = null;
      this.router.navigate(['/login'], { relativeTo: this.activatedRoute });
    }, (error: Error) => {
      this.dataService.handleErrorResponse(error);
    });
  }

  private getPrograms() {
    this._httpService.getRequest(environment.PROGRAM_LIST, this.authService.token).subscribe((res) => {
      let body = res.body;
      this.programList = body['data'];
      this.dataService.programList = _.groupBy(this.programList, (item: any) => item.programid);
    }, (error: Error) => {
      this.dataService.handleErrorResponse(error);
    });
  }

  navigateToProgram(programId: number) {
    this.programList.forEach((el: any) => {
      if (el.programid === programId) {
        this.dataService.program = el;
      }
    });
    this.router.navigate(['/program'], { relativeTo: this.activatedRoute, queryParams: { programId: programId } });
  }

  ngOnDestroy() {
    delete this.programList;
    
    delete this.faBell;
    delete this.faBoxOpen;
    delete this.faIdCard;
    delete this.faPowerOff;
    delete this.faShoppingCart;
  }
}
