import { Component, OnInit, ViewChild, OnDestroy } from '@angular/core';
import { faStopwatch } from '@fortawesome/free-solid-svg-icons';
import {
  NgbModal,
  NgbModalOptions
} from '@ng-bootstrap/ng-bootstrap';
import { AuthService } from '../services/auth.service';
import { HttpService } from '../services/http.service';
import { Router, ActivatedRoute } from '@angular/router';
import { NgForm } from '@angular/forms';
import { environment } from '../../environments/environment';
import { DataService } from '../services/data.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
})
export class LoginComponent implements OnInit, OnDestroy {
  toggleBtn = {
    msgText: 'Don\'t have an account?',
    linkText: 'Sign Up',
    headerText: 'Account Login',
  };
  signInFormEnabled = true;
  timeLeft = environment.OTP_TIMEOUT;
  timerText: string;
  timeTracker: any;
  OTP: string = '';
  timerExpired = false;
  timerView = false;
  faStopwatch = faStopwatch;
  otpView = false;
  mobileNumber: string = '';
  forgotPwdModalRef: any;
  signUpModalRef: any;
  modalReference: any;
  forgotPwdform = { password: '', confirmPassword: '', mobileNumber: '' };
  @ViewChild('signUpForm', { static: false }) signUpForm: NgForm;
  @ViewChild('signInForm', { static: false }) signInForm: NgForm;

  constructor(
    private modalService: NgbModal,
    private authService: AuthService,
    private _httpService: HttpService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private dataService: DataService
  ) { }

  ngOnInit(): void { }
  // ========================================================================================================
  launchModal(ref: any, name: string) {
    const ngbModalOptions: NgbModalOptions = {
      backdrop: 'static',
      keyboard: false,
    };
    if (name === 'SignUp') {
      this.sendOTP();
    }
    this.modalReference = this.modalService.open(ref, ngbModalOptions);
    this.modalReference.result.then(
      (result: string) => { },
      (reason: any) => {
        this.otpView = false;
      }
    );
  }
  // ========================================================================================================
  toggleBtnHandler() {
    this.signInFormEnabled = !this.signInFormEnabled;
    if (this.signInFormEnabled) {
      this.toggleBtn = {
        msgText: 'Don\'t have an account?',
        linkText: 'Sign Up',
        headerText: 'Account Login',
      };
    } else {
      this.toggleBtn = {
        msgText: 'Existing User?',
        linkText: 'Sign In',
        headerText: 'Create Your Account',
      };
    }
  }
  // ========================================================================================================
  onOtpChange($event: any) {
    let ev = $event;
    this.OTP = ev.toString();
    if (this.OTP.length === 6) {
      if (!this.signInFormEnabled) {
        this.signUp(this.OTP);
      }
    }
  }
  // ========================================================================================================
  countdown() {
    if (this.timeLeft === 0) {
      clearTimeout(this.timeTracker);
      this.timeLeft = environment.OTP_TIMEOUT;
      this.timerText =
        'OTP entered is expired. Please generate a new one and try again';
      this.timerExpired = true;
    } else {
      this.timerView = true;
      this.timerExpired = false;
      this.timerText = 'OTP expires within ' + this.timeLeft + ' seconds';
      this.timeLeft--;
    }
  }
  // ========================================================================================================
  sendOTP() {
    let _url: string;
    if (!this.signInFormEnabled) {
      _url = environment.SIGN_UP_VERIFY;
    } else {
      _url = environment.FORGOT_PWD_VERIFY;
    }
    this._httpService.postRequest(_url, JSON.stringify({ mobile_number: this.mobileNumber }), '').subscribe((res: any) => {
      clearTimeout(this.timeTracker);
      this.timeLeft = environment.OTP_TIMEOUT;
      this.timerText = 'OTP expires within ' + this.timeLeft + ' seconds';
      this.timerView = false;
      this.timeTracker = setInterval(() => {
        this.countdown();
      }, 1000);
    }, (error: Error) => {
      this.dataService.handleErrorResponse(error);
    });
  }
  // ========================================================================================================
  enablePasswordInput() {
    this.sendOTP();
    this.otpView = true;
  }
  // ========================================================================================================
  login() {
    let formValues = this.signInForm.form.value;
    let body = {
      mobile_number: formValues.mobileNumber,
      password: formValues.password
    }
    this.authService.login(environment.LOGIN, JSON.stringify(body)).subscribe((res: any) => {
      let body = res.body;
      this.postAuth(body);
    }, (error: Error) => {
      this.dataService.handleErrorResponse(error);
    });
  }
  // ========================================================================================================
  forgotPassword() {
    let body = {
      mobile_number: this.forgotPwdform.mobileNumber,
      password: this.forgotPwdform.confirmPassword,
      otp: this.OTP
    }
    this._httpService.postRequest(environment.FORGOT_PWD, JSON.stringify(body), '').subscribe((res: any) => {
      let body = res.body;
    }, (error: Error) => {
      this.dataService.handleErrorResponse(error);
    });
  }
  // ========================================================================================================
  signUp(OTP: string) {
    let formValues = this.signUpForm.form.value;
    let body = {
      firstname: formValues.firstName,
      lastname: formValues.lastName,
      email: formValues.emailID,
      password: formValues.password,
      mobile_number: this.mobileNumber,
      userrole: 'Consumer',
      otp: OTP,
    }
    this._httpService.postRequest(environment.SIGN_UP, JSON.stringify(body), '').subscribe((res: any) => {
      let body = res.body;
      this.postAuth(body);
      this.modalReference.close();
    }, (error: Error) => {
      this.dataService.handleErrorResponse(error);
    });
  }
  // ========================================================================================================
  postAuth(body: any) {
    this.authService.token = body.token;
    delete body.token;
    this.authService.usrObj = body;
    this.authService.clientName = body.firstname.charAt(0).toUpperCase() + body.firstname.slice(1) + ' ' + body.lastname.charAt(0).toUpperCase() + body.lastname.slice(1);
    this.authService.isLoggedIn = true;
    sessionStorage.setItem('token', this.authService.token);
    let cartItem = localStorage.getItem('cart');
    if(cartItem) {
      let cartArr = JSON.parse(cartItem);
      this.dataService.cartData = cartArr;
      this.dataService.cartItems = cartArr.length;
    } else {
      this.dataService.cartItems = 0;
      this.dataService.cartData = undefined;
    }
    if (this.authService.redirectUrl) {
      this.router.navigate([this.authService.redirectUrl]);
    } else {
      this.router.navigate(['/profile'], { relativeTo: this.activatedRoute });
    }
  }
  // ========================================================================================================
  ngOnDestroy(): void {
    delete this.toggleBtn;
    delete this.signInFormEnabled;
    delete this.timeLeft;
    delete this.timerText;
    delete this.timeTracker;
    delete this.timerView;
    delete this.faStopwatch;
    delete this.otpView;
    delete this.mobileNumber;
    delete this.forgotPwdModalRef;
    delete this.signUpModalRef;
    delete this.modalReference;
    delete this.forgotPwdform;
    delete this.signInForm;
    delete this.signUpForm;
  }
  // ========================================================================================================
}
