import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';

@Injectable()
export class HttpService {

    public _url: string = "https://juicedelivery.herokuapp.com/";

    constructor(private _httpClient: HttpClient) { }

    getRequest(uri: string, token: string) {

        let headers = new HttpHeaders({ 'Authorization': 'Bearer ' + sessionStorage.getItem('token') });
        return this._httpClient.get(this._url + encodeURI(uri), { headers: headers, observe: 'response' })
            .pipe(catchError(this.errorHandler));
    }

    postRequest(uri: string, body: string, token: string) {
        let headers = new HttpHeaders({ 'Content-Type': 'application/json', 'Authorization': 'Bearer ' + sessionStorage.getItem('token') });
        return this._httpClient.post(this._url + uri, body, { headers: headers, observe: 'response' })
            .pipe(catchError(this.errorHandler));
    }

    putRequest(uri: string, body: string, token: string) {
        let headers = new HttpHeaders({ 'Content-Type': 'application/json', 'Authorization': 'Bearer ' + sessionStorage.getItem('token') });
        return this._httpClient.put(this._url + uri, body, { headers: headers, observe: 'response' })
            .pipe(catchError(this.errorHandler));
    }

    deleteRequest(uri: string, token: string) {
        let headers = new HttpHeaders({ 'Authorization': 'Bearer ' + sessionStorage.getItem('token') });
        return this._httpClient.delete(this._url + encodeURI(uri), { headers: headers, observe: 'response' })
            .pipe(catchError(this.errorHandler));
    }

    fileDownload(uri: string, token: string) {
        let headers = new HttpHeaders({ 'Authorization': 'Bearer ' + sessionStorage.getItem('token') });
        return this._httpClient.get(this._url + encodeURI(uri), { headers: headers, observe: 'response', responseType: 'blob' })
            .pipe(catchError(this.errorHandler));
    }

    GetSortOrder(prop) {
        return function (a, b) {
            if (a[prop] > b[prop]) {
                return 1;
            } else if (a[prop] < b[prop]) {
                return -1;
            }
            return 0;
        }
    }

    private errorHandler(error: HttpErrorResponse) {
        return throwError(error || "Server Error");
    };
}